export default function getSeries(filterText) {
  filterText = filterText ? filterText.replace(' ','_') : '';

  return new Promise((resolve, reject) => {
    const xhr = new XMLHttpRequest();
    xhr.open('GET', `http://www.omdbapi.com/?apikey=d6985468&type=series&s=${filterText}`);
    xhr.send();

    xhr.onload = () => {
      if (xhr.status >= 200 && xhr.status < 300) {
        var json = JSON.parse(xhr.response);
        var searches = json.Search.filter(item => item.Poster != null && item.Poster != "N/A");
        setTimeout(resolve(searches), 2000);
      } else {
        reject()
      }
    };
  });
}